  "code": "<pre class=\"hljs\"><code><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">template</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">v-chart</span> <span class=\"hljs-attr\">:data</span>=<span class=\"hljs-string\">\"data\"</span> <span class=\"hljs-attr\">ref</span>=<span class=\"hljs-string\">\"demo\"</span>&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">v-scale</span> <span class=\"hljs-attr\">x</span> <span class=\"hljs-attr\">type</span>=<span class=\"hljs-string\">\"timeCat\"</span> <span class=\"hljs-attr\">:tick-count</span>=<span class=\"hljs-string\">\"3\"</span> <span class=\"hljs-attr\">mask</span>=<span class=\"hljs-string\">\"hh:mm:ss\"</span> /&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">v-scale</span> <span class=\"hljs-attr\">y</span> <span class=\"hljs-attr\">:min</span>=<span class=\"hljs-string\">\"8\"</span> <span class=\"hljs-attr\">:tick-count</span>=<span class=\"hljs-string\">\"5\"</span> /&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">v-tooltip</span> <span class=\"hljs-attr\">:show-item-marker</span>=<span class=\"hljs-string\">\"false\"</span> <span class=\"hljs-attr\">show-x-value</span> /&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">v-guide</span> <span class=\"hljs-attr\">type</span>=<span class=\"hljs-string\">\"html\"</span> <span class=\"hljs-attr\">:options</span>=<span class=\"hljs-string\">\"guide1\"</span> /&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">v-guide</span> <span class=\"hljs-attr\">type</span>=<span class=\"hljs-string\">\"html\"</span> <span class=\"hljs-attr\">:options</span>=<span class=\"hljs-string\">\"guide2\"</span> /&gt;</span>\n      <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">v-line</span> /&gt;</span>\n    <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">v-chart</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">template</span>&gt;</span>\n\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">script</span>&gt;</span><span class=\"javascript\">\n<span class=\"hljs-keyword\">import</span> { VChart, VTooltip, VLine, VScale, VGuide } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'vux'</span>\n\n<span class=\"hljs-keyword\">const</span> data = []\n\n<span class=\"hljs-comment\">// 添加数据，模拟数据，可以指定当前时间的偏移的秒</span>\n<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">getRecord</span> (<span class=\"hljs-params\">offset</span>) </span>{\n  offset = offset || <span class=\"hljs-number\">0</span>\n  <span class=\"hljs-keyword\">return</span> {\n    <span class=\"hljs-attr\">time</span>: <span class=\"hljs-keyword\">new</span> <span class=\"hljs-built_in\">Date</span>().getTime() + offset * <span class=\"hljs-number\">1000</span>,\n    <span class=\"hljs-attr\">value</span>: <span class=\"hljs-built_in\">Math</span>.random() + <span class=\"hljs-number\">10</span>\n  }\n}\n\ndata.push(getRecord(<span class=\"hljs-number\">-2</span>))\ndata.push(getRecord(<span class=\"hljs-number\">-1</span>))\ndata.push(getRecord())\n\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">default</span> {\n  <span class=\"hljs-attr\">components</span>: {\n    VChart,\n    VTooltip,\n    VLine,\n    VScale,\n    VGuide\n  },\n  mounted () {\n    <span class=\"hljs-keyword\">this</span>.timer = setInterval(<span class=\"hljs-function\"><span class=\"hljs-params\">()</span> =&gt;</span> {\n      data.push(getRecord())\n    }, <span class=\"hljs-number\">2000</span>)\n  },\n  beforeDestroy () {\n    clearInterval(<span class=\"hljs-keyword\">this</span>.timer)\n  },\n  data () {\n    <span class=\"hljs-keyword\">return</span> {\n      <span class=\"hljs-attr\">guide1</span>: {\n        position (xScale, yScales) {\n          <span class=\"hljs-keyword\">const</span> xValues = xScale.values\n          <span class=\"hljs-keyword\">const</span> yValues = yScales[<span class=\"hljs-number\">0</span>].values\n          <span class=\"hljs-keyword\">const</span> xMax = xValues[xValues.length - <span class=\"hljs-number\">1</span>]\n          <span class=\"hljs-keyword\">const</span> yMax = yValues[yValues.length - <span class=\"hljs-number\">1</span>]\n          <span class=\"hljs-keyword\">return</span> [ xMax, yMax ]\n        },\n        <span class=\"hljs-attr\">html</span>: <span class=\"hljs-string\">'&lt;div style=\"border-radius: 100%;border: none;width: 12px;height: 12px;background-color: rgb(24, 144, 255);transition: top 0.35s cubic-bezier(0.23, 1, 0.32, 1);\"&gt;&lt;/div&gt;'</span>\n      },\n      <span class=\"hljs-attr\">guide2</span>: {\n        position (xScale, yScales) {\n          <span class=\"hljs-keyword\">const</span> xValues = xScale.values\n          <span class=\"hljs-keyword\">const</span> yValues = yScales[<span class=\"hljs-number\">0</span>].values\n          <span class=\"hljs-keyword\">const</span> xMax = xValues[xValues.length - <span class=\"hljs-number\">1</span>]\n          <span class=\"hljs-keyword\">const</span> yMax = yValues[yValues.length - <span class=\"hljs-number\">1</span>]\n          <span class=\"hljs-keyword\">return</span> [ xMax, yMax ]\n        },\n        <span class=\"hljs-attr\">html</span>: <span class=\"hljs-string\">'&lt;div style=\"border-radius: 100%;border: none;width: 20px;height: 20px;background-color: rgba(24, 144, 255, 0.5);transition: top 0.35s cubic-bezier(0.23, 1, 0.32, 1);\"&gt;&lt;/div&gt;'</span>\n      },\n      data\n    }\n  }\n}\n</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">script</span>&gt;</span>\n\n\n</code></pre>",
