    "title": "自定义渲染逻辑",
    "code": "<pre class=\"hljs\"><code><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">template</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">v-chart</span>\n      <span class=\"hljs-attr\">prevent-render</span>\n      @<span class=\"hljs-attr\">on-render</span>=<span class=\"hljs-string\">\"renderChart\"</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">v-chart</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">template</span>&gt;</span>\n\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">script</span>&gt;</span><span class=\"javascript\">\n<span class=\"hljs-keyword\">import</span> { VChart } <span class=\"hljs-keyword\">from</span> <span class=\"hljs-string\">'vux'</span>\n\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">default</span> {\n  <span class=\"hljs-attr\">components</span>: {\n    VChart\n  },\n  <span class=\"hljs-attr\">methods</span>: {\n    renderChart ({ chart }) {\n      <span class=\"hljs-keyword\">const</span> data = [ { <span class=\"hljs-attr\">x</span>: <span class=\"hljs-string\">'1'</span>, <span class=\"hljs-attr\">y</span>: <span class=\"hljs-number\">85</span> } ]\n      chart.source(data, {\n        <span class=\"hljs-attr\">y</span>: {\n          <span class=\"hljs-attr\">max</span>: <span class=\"hljs-number\">100</span>,\n          <span class=\"hljs-attr\">min</span>: <span class=\"hljs-number\">0</span>\n        }\n      })\n      chart.axis(<span class=\"hljs-literal\">false</span>)\n      chart.tooltip(<span class=\"hljs-literal\">false</span>)\n      chart.coord(<span class=\"hljs-string\">'polar'</span>, {\n        <span class=\"hljs-attr\">transposed</span>: <span class=\"hljs-literal\">true</span>,\n        <span class=\"hljs-attr\">innerRadius</span>: <span class=\"hljs-number\">0.8</span>,\n        <span class=\"hljs-attr\">radius</span>: <span class=\"hljs-number\">0.85</span>\n      })\n      chart.guide().arc({\n        <span class=\"hljs-attr\">start</span>: [ <span class=\"hljs-number\">0</span>, <span class=\"hljs-number\">0</span> ],\n        <span class=\"hljs-attr\">end</span>: [ <span class=\"hljs-number\">1</span>, <span class=\"hljs-number\">99.98</span> ],\n        <span class=\"hljs-attr\">top</span>: <span class=\"hljs-literal\">false</span>,\n        <span class=\"hljs-attr\">style</span>: {\n          <span class=\"hljs-attr\">lineWidth</span>: <span class=\"hljs-number\">20</span>,\n          <span class=\"hljs-attr\">stroke</span>: <span class=\"hljs-string\">'#ccc'</span>\n        }\n      })\n      chart.guide().text({\n        <span class=\"hljs-attr\">position</span>: [ <span class=\"hljs-string\">'50%'</span>, <span class=\"hljs-string\">'50%'</span> ],\n        <span class=\"hljs-attr\">content</span>: <span class=\"hljs-string\">'85%'</span>,\n        <span class=\"hljs-attr\">style</span>: {\n          <span class=\"hljs-attr\">fontSize</span>: <span class=\"hljs-number\">24</span>,\n          <span class=\"hljs-attr\">fill</span>: <span class=\"hljs-string\">'#1890FF'</span>\n        }\n      })\n      chart.interval()\n        .position(<span class=\"hljs-string\">'x*y'</span>)\n        .size(<span class=\"hljs-number\">20</span>)\n        .animate({\n          <span class=\"hljs-attr\">appear</span>: {\n            <span class=\"hljs-attr\">duration</span>: <span class=\"hljs-number\">1200</span>,\n            <span class=\"hljs-attr\">easing</span>: <span class=\"hljs-string\">'cubicIn'</span>\n          }\n        })\n      chart.render()\n    }\n  }\n}\n</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">script</span>&gt;</span>\n\n\n</code></pre>",
