=\"hljs-string\">'18%'</span>,\n  <span class=\"hljs-string\">'心理罪'</span>: <span class=\"hljs-string\">'15%'</span>,\n  <span class=\"hljs-string\">'寻梦环游记'</span>: <span class=\"hljs-string\">'5%'</span>,\n  <span class=\"hljs-string\">'其他'</span>: <span class=\"hljs-string\">'2%'</span>\n}\n\n<span class=\"hljs-keyword\">export</span> <span class=\"hljs-keyword\">default</span> {\n  <span class=\"hljs-attr\">components</span>: {\n    VChart,\n    VLine,\n    VArea,\n    VTooltip,\n    VLegend,\n    VBar,\n    VPie,\n    VScale\n  },\n  data () {\n    <span class=\"hljs-keyword\">return</span> {\n      <span class=\"hljs-attr\">legendOptions</span>: {\n        <span class=\"hljs-attr\">position</span>: <span class=\"hljs-string\">'right'</span>,\n        itemFormatter (val) {\n          <span class=\"hljs-keyword\">return</span> val + <span class=\"hljs-string\">'  '</span> + map[val]\n        }\n      },\n      <span class=\"hljs-attr\">yOptions</span>: {\n        formatter (val) {\n          <span class=\"hljs-keyword\">return</span> val * <span class=\"hljs-number\">100</span> + <span class=\"hljs-string\">'%'</span>\n        }\n      },\n      map,\n      <span class=\"hljs-attr\">data</span>: [\n        { <span class=\"hljs-attr\">name</span>: <span class=\"hljs-string\">'芳华'</span>, <span class=\"hljs-attr\">percent</span>: <span class=\"hljs-number\">0.4</span>, <span class=\"hljs-attr\">a</span>: <span class=\"hljs-string\">'1'</span> },\n        { <span class=\"hljs-attr\">name</span>: <span class=\"hljs-string\">'妖猫传'</span>, <span class=\"hljs-attr\">percent</span>: <span class=\"hljs-number\">0.2</span>, <span class=\"hljs-attr\">a</span>: <span class=\"hljs-string\">'1'</span> },\n        { <span class=\"hljs-attr\">name</span>: <span class=\"hljs-string\">'机器之血'</span>, <span class=\"hljs-attr\">percent</span>: <span class=\"hljs-number\">0.18</span>, <span class=\"hljs-attr\">a</span>: <span class=\"hljs-string\">'1'</span> },\n        { <span class=