attr\">data</span>: [\n        { <span class=\"hljs-attr\">time</span>: <span class=\"hljs-string\">'Jan.'</span>, <span class=\"hljs-attr\">tem</span>: <span class=\"hljs-number\">1000</span> },\n        { <span class=\"hljs-attr\">time</span>: <span class=\"hljs-string\">'Feb.'</span>, <span class=\"hljs-attr\">tem</span>: <span class=\"hljs-number\">2200</span> },\n        { <span class=\"hljs-attr\">time</span>: <span class=\"hljs-string\">'Mar.'</span>, <span class=\"hljs-attr\">tem</span>: <span class=\"hljs-number\">2000</span> },\n        { <span class=\"hljs-attr\">time</span>: <span class=\"hljs-string\">'Apr.'</span>, <span class=\"hljs-attr\">tem</span>: <span class=\"hljs-number\">2600</span> },\n        { <span class=\"hljs-attr\">time</span>: <span class=\"hljs-string\">'May.'</span>, <span class=\"hljs-attr\">tem</span>: <span class=\"hljs-number\">2000</span> },\n        { <span class=\"hljs-attr\">time</span>: <span class=\"hljs-string\">'Jun.'</span>, <span class=\"hljs-attr\">tem</span>: <span class=\"hljs-number\">2600</span> },\n        { <span class=\"hljs-attr\">time</span>: <span class=\"hljs-string\">'Jul.'</span>, <span class=\"hljs-attr\">tem</span>: <span class=\"hljs-number\">2800</span> },\n        { <span class=\"hljs-attr\">time</span>: <span class=\"hljs-string\">'Aug.'</span>, <span class=\"hljs-attr\">tem</span>: <span class=\"hljs-number\">2000</span> }\n      ]\n    }\n  }\n}\n</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">script</span>&gt;</span>\n\n</code></pre>",
