Vue.component('v-guide', VGuide)
Vue.component('v-tooltip', VTooltip)
Vue.component('v-legend', VLegend)
Vue.component('v-guide', VGuide)
`,
        needImport: true,
        toc: [{"title":"Intro","anchor":"intro"},{"title":"Install","anchor":"install"},{"title":"Examples","anchor":"examples","list":[{"title":"折线图：显示点","anchor":"examples:折线图：显示点"},{"title":"折线图:平滑曲线","anchor":"examples:折线图:平滑曲线"},{"title":"动态数据：实时折线","anchor":"examples:动态数据：实时折线"},{"title":"折线图：渐变","anchor":"examples:折线图：渐变"},{"title":"折线图:对比","anchor":"examples:折线图:对比"}]},{"title":"API","anchor":"api","list":[{"title":"<v-chart>","anchor":"components:v-chart"},{"title":"<v-line>","anchor":"components:v-line"},{"title":"<v-area>","anchor":"components:v-area"},{"title":"<v-bar>","anchor":"components:v-bar"},{"title":"<v-pie>","anchor":"components:v-pie"},{"title":"<v-point>","anchor":"components:v-point"},{"title":"<v-scale>","anchor":"components:v-scale"},{"title":"<v-axis>","anchor":"components:v-axis"},{"title":"<v-guide>","anchor":"components:v-guide"},{"title":"<v-tooltip>","anchor":"components:v-tooltip"},{"title":"<v-legend>","anchor":"components:v-legend"},{"title":"<v-guide>","anchor":"components:v-guide"}]},{"title":"Tips","anchor":"tips"},{"title":"Contributors","anchor":"contributors"},{"title":"Changelog","anchor":"changelog"}],
        domain: domainMap[process.env.NODE_ENV]
      }
    }
  }
  </script>

  <style>
  .component-box {
    position: relative;
  }
